import React from 'react'


const ProblemSelector = (props) => {
  if(!props.showingProblem) {
    return (
      <div className="jumbotron">
        <p className="lead">Um Coding Dojo  um encontro onde um grupo de programadores se rene para treinar tcnicas e metodologias de desenvolvimento de software atravs da soluo de um pequeno desafio de programao utilizando boas prticas de programao.</p>
        <p><span
          onClick={props.onFindProblemClick}
          className="btn btn-lg btn-success"
          role="button">Encontre um problema</span></p>
      </div>
    )
  } else if (!props.problemSelected) {
    return (
      <div>
        <ul className="nav nav-pills nav-fill">
          <li className="nav-item">
            <span
              onClick={props.onProblemSelectedClick}
              className="btn btn-success"
              role="button">Gostei! Vou usar este!</span>
          </li>
          <li className="nav-item">
            <span
              onClick={props.onFindProblemClick}
              className="btn btn-danger"
              role="button">Mostre-me outro problema!</span>
          </li>
        </ul>
      </div>
    )   
  } else {
    return <div />
  }

}

export default ProblemSelector


